* {
  box-sizing: border-box;
  outline: none;
  margin: 0;
}

body,
html {
  height: 100%;
  background-color: #f1f1f1;
}

/*************************** FONTS ***************************/

@font-face {
  font-family: "namaste";
  src: local("namaste"),
    url(./assets/fonts/SamarkanNormal-Gg5D.ttf) format("truetype");
}

@font-face {
  font-family: "retro";
  src: local("retro"),
    url(./assets/fonts/NineteenNinetyThree-L1Ay.ttf) format("truetype");
}

@font-face {
  font-family: "thin-tall";
  src: local("thin-tall"),
    url(./assets/fonts/GreenHostel-vma2y.ttf) format("truetype");
}

/*************************** NAVBAR  ***************************/

.nav-bar {
  display: flex;
  flex-direction: row;
  padding: 15px 10%;
  justify-content: space-between;
  background-color: #111111;
  border-bottom: 3px solid yellow;
  width: 100%;
  position: fixed;
  z-index: 100;
}

.hero a {
  font-size: 30px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin: auto;
  text-decoration: none;
  color: #dadada;
}

.nav-links {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.nav-link {
  border: 0.5px solid #494949;
  margin: 0px 2px;

  transition-duration: 300ms;
}

.nav-link:hover {
  border-color: #dadada;

  transition-duration: 300ms;
}

.nav-link a {
  display: flex;
  justify-content: center;
  width: 120px;
  padding: 9px 0;
  background: transparent;

  font-size: 15px;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 0.25px;
  text-decoration: none;
  color: #dadada;
}

.nav-link a:hover {
  transition-duration: 400ms;
}

.rand-color-1:hover {
  color: #55dd48;
}

.rand-color-2:hover {
  color: #e67bd7;
}

.rand-color-3:hover {
  color: #aac1f3;
}

.rand-color-4:hover {
  color: #42cec9;
}

.rand-color-5:hover {
  color: #f4a72c;
}

/*************************** HOME PAGE ***************************/

.home {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
}

.home .left {
  display: flex;
  height: 100vh;
  width: 50%;
  align-items: center;
  background-position: left top;
  /* flex-grow: 1; */
}

.home .right {
  display: flex;
  height: 100vh;
  width: 50%;
  align-items: center;
  background-position: right top;
}

.image-align {
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 50%;
  opacity: 0.9;
}

.rohan {
  background-image: url("assets/images/rohan.jpeg");
}

@media screen and (max-width: 1300px) {
  .home {
    flex-direction: column;
    height: 90%;
  }

  .home .left,
  .home .right {
    height: 90vh;
    width: 100%;
    background-size: 100%;
  }
}

.home .info {
  margin-left: 10%;
  color: #111111;
}

/* TODO: change font */
.home .info .heading {
  font-family: "Open Sans", sans-serif;
  font-size: 100px;
  font-weight: 700;
  letter-spacing: -5px;
  text-shadow: -8px 8px yellow;
}

.home .info .heading .namaste {
  font-family: "namaste", sans-serif;
  letter-spacing: 2px;
}

.home .info .sub-heading {
  font-family: "Open Sans", sans-serif;
  font-size: 50px;
  letter-spacing: -2px;
  font-weight: 100;
}

/************************** PROJECTS ************************/

.projects {
  display: flex;
  flex-direction: row;
}

.dots {
  background-image: url("assets/images/dot.png");
  background-position: left top;
}

.projects .left {
  display: flex;
  /* TODO change height */
  height: 1000vh;
  width: 50%;
  align-items: center;
}

.projects .right {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 50%;
  align-items: center;
  justify-content: center;
}

.projects .right .container {
  position: fixed;
}

.projects .right .info {
  display: flex;
  flex-direction: column;
  background-color: white;
  border: 2px solid #333;
  box-shadow: 10px 10px #111111;
  padding: 40px 40px;
}

.projects .right .info .heading {
  font-family: "retro", sans-serif;
  font-size: 100px;
  letter-spacing: -5px;
}

.projects .right .info .body {
  font-family: monospace;
  font-size: 25px;
  font-weight: 100;
}

.projects .right .info .matter {
  font-family: monospace;
  font-size: 20px;
  letter-spacing: -1px;
  margin: 40px 0;
}

.projects .right .container a {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  border: 2px solid #333;
  box-shadow: 10px 10px #111111;
  padding: 15px 40px;
  text-decoration: none;
  font-family: monospace;
  font-size: 20px;
  color: #111111;

  transition-duration: 300ms;
}

.projects .right .container a:hover {
  box-shadow: 1px 1px #111111;

  transition-duration: 300ms;
}

.projects .right .container a span {
  font-family: "retro";
  font-size: 24px;
}

@media screen and (max-width: 1300px) {
  .projects {
    flex-direction: column;
    /* height: 90%; */
  }

  .projects .left {
    height: 100vh;
    width: 100%;
  }

  .projects .right {
    margin-bottom: 100px;
    height: fit-content;
    width: 100%;
  }

  .projects .right .container {
    position: relative;
  }
}

/* marquee */

.marquee {
  position: absolute;
  width: 100%;
  bottom: 10%;
  line-height: 35px;
  background-color: yellow;
  color: #111111;
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
  z-index: 20;
}

.marquee p {
  display: inline-block;
  font-family: monospace;
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: -1px;
  padding-left: 100%;
  animation: marquee 20s linear infinite;
}
@keyframes marquee {
  0% {
    transform: translate(100%, 0);
  }
  100% {
    transform: translate(-100%, 0);
  }

  marquee {
    position: absolute;
    width: 100%;
    bottom: 10%;
  }
}
